	module.exports = {"data":{"post":{"html":"<p><a title=\"Kino: 'Gegen die Wand' bester europäischer Film - Kultur - SPIEGEL ONLINE\" href=\"http://www.spiegel.de/kultur/kino/0,1518,332480,00.html\">SPON: \"Gegen die Wand\" best European film.</a> Also <a title=\"Entertainment News Article | Reuters.co.uk\" href=\"http://www.reuters.co.uk/newsArticle.jhtml?type=entertainmentNews&storyID=637121\">Reuters</a>:  </p>\n<blockquote><p>\"Gegen Die Wand\" (\"Head-On\"), about a young Turkish woman in Germany who escapes her strict Muslim home through a difficult marriage with an older man, won the best film award in the competition dubbed the \"European Oscars\" on Saturday... The European Film Awards sealed the remarkable success of \"Gegen Die Wand\", which overcame a scandal over its star's porn movie past to score a German box-office success and win major prizes including the Golden Bear at the Berlin Film Festival. As well as best film, Turkish-German Fatih Akin collected the best director award for \"Gegen Die Wand\" in the People's Choice category on Saturday.\"</p></blockquote>\n<p>Akin lives and works in Hamburg-Altona, and much of the film was shot there.</p>","fields":{"title":"Best European Film","date":"12 Dec 2004","url":"https://papascott.de/2004-12-12-best-european-film/"}}},"pathContext":{"slug":"/2004-12-12-best-european-film/","prev":{"fields":{"title":"stopping bullets with your teeth","date":"10 Dec 2004","slug":"/2004-12-10-stopping-bullets-with-your-teeth/"}},"next":{"fields":{"title":"MDAC Insanity","date":"12 Dec 2004","slug":"/2004-12-12-mdac-insanity/"}}}}

